@import "functions";
@import "placeholders";


$user-content: 'user-content' !default;

// ##########################################################################


// Special Classes
// ==========================================================================
$spec-mod: 'vspec' !default;
$spec-mod--image-on: 'vspec-mod_img_on' !default;
$spec-mod--image-off: 'vspec-mod_img_off' !default;
$spec-mod--mono: 'vspec-mod_mono' !default;
$spec-mod--sans-serif: 'vspec-mod_font-style_sans-serif' !default;
$spec-mod--serif: 'vspec-mod_font-style_serif' !default;
$spec-mod--monospace: 'vspec-mod_font-style_monospace' !default;
$spec-mod--font-small: 'vspec-mod_font-size_sm' !default;
$spec-mod--font-normal: 'vspec-mod_font-size_norm' !default;
$spec-mod--font-large: 'vspec-mod_font-size_lg' !default;
$spec-mod--line-height-small: 'vspec-mod_line-height_sm' !default;
$spec-mod--line-height-medium: 'vspec-mod_line-height_md' !default;
$spec-mod--line-height-large: 'vspec-mod_line-height_lg' !default;
$spec-mod--spacing-normal: 'vspec-mod_spacing_norm' !default;
$spec-mod--spacing-medium: 'vspec-mod_spacing_md' !default;
$spec-mod--spacing-large: 'vspec-mod_spacing_lg' !default;
$spec-mod--scheme-def: 'vspec-mod_scheme-color_def' !default;
$spec-mod--scheme-wb: 'vspec-mod_scheme-color_white' !default;
$spec-mod--scheme-blue: 'vspec-mod_scheme-color_blue' !default;
$spec-mod--scheme-brown: 'vspec-mod_scheme-color_brown' !default;
$spec-mod--scheme-green: 'vspec-mod_scheme-color_green' !default;

// Special Panel
// ==========================================================================
//$spec-panel-size: 100% 70px // height width
$spec-panel-size: 74px auto; // height width
$spec-panel-size--sm: 56px auto; // height width
$spec-panel-inner-space: 16px;
$spec-panel-inner-space--sm: 12px;
$spec-panel__btn-size: 42px;
$spec-panel__btn-size--sm: 36px;
$spec-panel__btn-arr-size: 24px;
$spec-panel__btn-shadow-inside: 0 0 0 2px;
$spec-panel__btn-shadow-outside: 0 0 0 5px;
$spec-panel__btn-shadow-hover: inset 0 0 0 3px;


// СПЕЦИАЛЬНАЯ ВЕРСИЯ: НАСТРОЙКИ
// ==========================================================================

// >>> Color scheme <<<
// ==========================================================================

// Специальная версия: Цветовая схема - Черным по белому (стандартная)
// ---------------------------------------------------------------------------
$spec-text-color: #000 !default;
$spec-text-color--scheme-def: $spec-text-color !default;
$spec-bg-color: #fff !default;
$spec-bg-color--scheme-def: $spec-bg-color !default;

// Специальная версия: Цветовая схема - Белым по черному
// ---------------------------------------------------------------------------
$spec-text-color--scheme-wb: #fff !default;
$spec-bg-color--scheme-wb: #000 !default;

// Специальная версия: Цветовая схема - Синим по голубому
// ---------------------------------------------------------------------------
$spec-text-color--scheme-blue: #004c92 !default;
$spec-bg-color--scheme-blue: #c8e5ff !default;

// Специальная версия: Цветовая схема - Коричневым по бежевому
// ---------------------------------------------------------------------------
$spec-text-color--scheme-brown: #4d4b43 !default;
$spec-bg-color--scheme-brown: #f7f3d6 !default;

// Специальная версия: Цветовая схема - Зеленым по темно-коричневому
// ---------------------------------------------------------------------------
$spec-text-color--scheme-green: #a9e44d !default;
$spec-bg-color--scheme-green: #3b2716 !default;


// >>> Font Style <<<
// ==========================================================================

// Специальная версия: Начертание шрифта - Без засечками (serif)
// ---------------------------------------------------------------------------
//$spec-sans-serif: Arial, Helvetica, Geneva, "Trebuchet MS", sans-serif !default
$spec-font-style--sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;

// Специальная версия: Начертание шрифта - С засечками (serif)
// ---------------------------------------------------------------------------
//$spec-serif: Bookman, serif !default
$spec-font-style--serif: "Times New Roman", Times, Georgia, serif !default;

// Специальная версия: Начертание шрифта - Моноширинный шрифт (monospace)
// ---------------------------------------------------------------------------
$spec-font-style--monospace: Consolas, Monaco, 'Lucida Console', 'Liberation Mono', 'DejaVu Sans Mono', 'Bitstream Vera Sans Mono', 'Courier New', monospace !default;


// >>> Font Size <<<
// ==========================================================================

// Специальная версия: Размер шрифта - Стандартный базовый шрифт
// ---------------------------------------------------------------------------
$spec-font-size: 18px !default; // 18px (1em)
$spec-font-size--mob: 16px !default; // 18px (1em)
$spec-font-size--medium: $spec-font-size !default;

//$spec-line-height: round-float(30px / $spec-font-size, 2) !default // 30px --> 1.67

// Специальная версия: Размер шрифта - Уменьшенный базовый шрифт
// ---------------------------------------------------------------------------
$spec-font-size--small: 14px !default;
//$spec-line-height--small: round-float(24px / $spec-font-size--small, 2) !default // 24px --> 1.71

// Специальная версия: Размер шрифта - Увеличенный базовый шрифт
// ---------------------------------------------------------------------------
$spec-font-size--large: 30px !default;
//$spec-line-height--large: round-float(45px / $spec-font-size--large, 2) !default // 45px --> 1.5


// >>> Line Height (Line Spacing) <<<
// ==========================================================================

// Специальная версия: Междустрочный интервал - Одинарный (нормальный)
// ---------------------------------------------------------------------------
$spec-line-height--small: 1.5 !default;

// Специальная версия: Междустрочный интервал - Полуторный
// ---------------------------------------------------------------------------
$spec-line-height--medium: 2.0 !default;

// Специальная версия: Междустрочный интервал - Двойной
// ---------------------------------------------------------------------------
$spec-line-height--large: 2.5 !default;


// >>> Letter Spacing (Kerning) <<<
// ==========================================================================

// Специальная версия: Межбуквенный интервал - Базовый (нормальный)
// ---------------------------------------------------------------------------
$spec-letter-spacing--normal: normal !default;

// Специальная версия: Межбуквенный интервал - Средний
// ---------------------------------------------------------------------------
$spec-letter-spacing--medium: 0.2em !default;

// Специальная версия: Межбуквенный интервал - Бальшой
// ---------------------------------------------------------------------------
$spec-letter-spacing--large: 0.5em !default;


// Группы текста по размерам
// ==========================================================================

// Стандартный базовый шрифт
// ---------------------------------------------------------------------------
@mixin spec-font-size--normal() {
	font-size: $spec-font-size;
 }	//line-height: $spec-line-height

// Уменьшенный базовый шрифт
// ---------------------------------------------------------------------------
@mixin spec-font-size--small() {
	font-size: $spec-font-size--small;
 }	//line-height: $spec-line-height--small

// Увеличенный базовый шрифт
// ---------------------------------------------------------------------------
@mixin spec-font-size--large() {
	font-size: $spec-font-size--large;
 }	//line-height: $spec-line-height--large


// H1, Заголовок страницы
// ---------------------------------------------------------------------------
//$spec-title-font-size: 36px !default // В px. Менять размер через панель настроект нет необходимости
$spec-title-font-size: round-float(32px / $spec-font-size, 1) * 1em !default; // 36px --> 2em
$spec-title-line-height: round-float(48px / 36px, 1) !default; // 48px --> 1.33

@mixin spec-font-size--title() {
	font-size: $spec-title-font-size;
	line-height: $spec-title-line-height;

	@include media('<tab-lg') {
		font-size: round-float(28px / $spec-font-size, 1) * 1em; }

	@include media('<mob-lg') {
		font-size: round-float(24px / $spec-font-size, 1) * 1em; } }

// H2, Заголовки секций
// ---------------------------------------------------------------------------
$spec-section-font-size: round-float(26px / $spec-font-size, 1) * 1em !default; // 26px --> 1.44em
$spec-section-line-height: round-float(38px / 26px, 1) !default; // 38px --> 1.44

@mixin spec-font-size--section() {
	font-size: $spec-section-font-size;
	line-height: $spec-section-line-height;

	@include media('<tab-lg') {
		font-size: round-float(24px / $spec-font-size, 1) * 1em; }

	@include media('<mob-lg') {
		font-size: round-float(22px / $spec-font-size, 1) * 1em; } }

// H3, Заголовки пунктов
// ---------------------------------------------------------------------------
$spec-item-font-size: round-float(22px / $spec-font-size, 1) * 1em !default; // 22px --> 1.22em
$spec-item-line-height: round-float(34px / 22px, 1) !default; // 34px --> 1.55

@mixin spec-font-size--item() {
	font-size: $spec-item-font-size;
	line-height: $spec-item-line-height; }

// Мелкий текст: сноски, даты и т.д.
// ---------------------------------------------------------------------------
$spec-small-text-font-size: round-float(16px / $spec-font-size, 1) * 1em !default; // 16px --> 0.89em
$spec-small-text-line-height: round-float(24px / 16px, 1) !default; // 28px --> 1.75

@mixin spec-font-size--note() {
	font-size: $spec-small-text-font-size;
	line-height: $spec-small-text-line-height; }


// Отступы
// ==========================================================================

// Отступы горизонтальные
// ---------------------------------------------------------------------------
$spec-space: 1.5em !default;
$spec-space--sm: 1.25em !default;
$spec-space--mob: 1em !default;
$spec-page-space: (round-float(40px / $spec-font-size, 2) * 1em) (round-float(40px / $spec-font-size, 2) * 1em) !default;
$spec-page-space--md: (round-float(20px / $spec-font-size, 2) * 1em) (round-float(20px / $spec-font-size, 2) * 1em) !default;
$spec-page-space--sm: (round-float(15px / $spec-font-size, 2) * 1em) (round-float(15px / $spec-font-size, 2) * 1em) !default;

// Максимальная ширина контента
// ---------------------------------------------------------------------------
$spec-text-page: 50em !default;
//$spec-text-page: 60em !default
$spec-text-layout: 44em !default; // Задавать нужно в em, чтобы при изменении размера шрифта, количество символов в строке сохранялось. Примерно 85 символов в строке
$spec-text-layout--sm: 36em !default; // Для небольших экранов
$spec-form-layout: 32em !default;

// Отступы вертикальные
// ---------------------------------------------------------------------------
$spec-bottom-space: 1.2em !default;
$spec-bottom-space--sm: 0.8em !default;
// ------------------
$spec-bottom-space-element: round-float($spec-bottom-space / 2, 1) !default;
$spec-bottom-space-element--sm: round-float($spec-bottom-space--sm / 2, 1) !default;
// ------------------
$spec-bottom-space-item: round-float($spec-bottom-space * 1.5, 1) !default;
$spec-bottom-space-item--sm: round-float($spec-bottom-space--sm * 1.5, 1) !default;
// ------------------
$spec-bottom-space-section: round-float($spec-bottom-space * 2.2, 1) !default;
$spec-bottom-space-section--sm: round-float($spec-bottom-space--sm * 2.2, 1) !default;


// Borders
// ==========================================================================
$spec-border-width: 2px;
$spec-border-radius: 6px;

// Reset font style
@mixin spec-font-reset() {
	font: {
		family: inherit;
		weight: inherit;
		style: inherit;
		size: inherit; }
	line-height: inherit;
	letter-spacing: inherit;
	color: inherit;
	text-align: left;
	text-transform: none;
	white-space: normal; }

// Reset background
@mixin spec-reset-bg() {
	background-color: $spec-bg-color;

	.#{$spec-mod--scheme-wb} & {
		background-color: $spec-bg-color--scheme-wb; }

	.#{$spec-mod--scheme-blue} & {
		background-color: $spec-bg-color--scheme-blue; }

	.#{$spec-mod--scheme-brown} & {
		background-color: $spec-bg-color--scheme-brown; }

	.#{$spec-mod--scheme-green} & {
		background-color: $spec-bg-color--scheme-green; } }

// Reset background + invert
// @example: buttons, table headings, mark elements
@mixin spec-reset-bg-invert() {
	color: $spec-bg-color;
	background-color: $spec-text-color;
	border-color: $spec-text-color;

	.#{$spec-mod--scheme-wb} & {
		color: $spec-bg-color--scheme-wb;
		background-color: $spec-text-color--scheme-wb;
		border-color: $spec-text-color--scheme-wb; }

	.#{$spec-mod--scheme-blue} & {
		color: $spec-bg-color--scheme-blue;
		background-color: $spec-text-color--scheme-blue;
		border-color: $spec-text-color--scheme-blue; }

	.#{$spec-mod--scheme-brown} & {
		color: $spec-bg-color--scheme-brown;
		background-color: $spec-text-color--scheme-brown;
		border-color: $spec-text-color--scheme-brown; }

	.#{$spec-mod--scheme-green} & {
		color: $spec-bg-color--scheme-green;
		background-color: $spec-text-color--scheme-green;
		border-color: $spec-text-color--scheme-green; } }

// Special gradient background
@mixin spec-bg-gradient($direct: 180deg) {
	@include linear-gradient($direct, tint($spec-text-color, 1%) 0%, tint($spec-text-color, 10%) 100%);

	.#{$spec-mod--scheme-wb} & {
		@include linear-gradient($direct, tint($spec-bg-color--scheme-wb, 50%) 0%, $spec-bg-color--scheme-wb 100%); }

	.#{$spec-mod--scheme-blue} & {
		@include linear-gradient($direct, tint($spec-bg-color--scheme-blue, 20%) 0%, $spec-bg-color--scheme-blue 100%); }

	.#{$spec-mod--scheme-brown} & {
		@include linear-gradient($direct, tint($spec-bg-color--scheme-brown, 70%) 0%, $spec-bg-color--scheme-brown 100%); }

	.#{$spec-mod--scheme-green} & {
		@include linear-gradient($direct, tint($spec-bg-color--scheme-green, 70%) 0%, $spec-bg-color--scheme-green 100%); } }

// Reset border color
@mixin spec-reset-border() {
	//border-width: $spec-border-width
	border-color: $spec-text-color;

	.#{$spec-mod--scheme-wb} & {
		border-color: $spec-text-color--scheme-wb; }

	.#{$spec-mod--scheme-blue} & {
		border-color: $spec-text-color--scheme-blue; }

	.#{$spec-mod--scheme-brown} & {
		border-color: $spec-text-color--scheme-brown; }

	.#{$spec-mod--scheme-green} & {
		border-color: $spec-text-color--scheme-green; } }

// Reset all styles
@mixin spec-reset-full() {
	display: block;
	//display: initial
	float: none;
	//position: initial
	position: static;
	overflow: visible;
	height: auto;
	min-height: 0;
	max-height: none;
	width: auto;
	min-width: 0;
	max-width: none;
	left: auto;
	top: auto;
	right: auto;
	bottom: auto;
	padding: 0;
	margin: 0;
	transform: none;
	border: none;
	background: none;
	box-shadow: none;
	opacity: 1;
	visibility: visible;
	z-index: auto;
	font: {
		family: inherit;
		weight: inherit;
		style: inherit;
		size: inherit; }
	text-indent: 0;
	line-height: inherit;
	letter-spacing: inherit;
	word-spacing: inherit;
	color: inherit;
	text-align: left;
	text-transform: none;
	text-shadow: none;
	box-sizing: border-box;
	border-radius: 0;
	perspective: none;
	vertical-align: baseline; }

// Список классов-исключений для тегов ul, ol
// Добавляются к типографическим стилям списков ul, ol, чтобы не применять стили для указанных классов
// Example: $not-list-classes: ':not(.class1):not(.class2)'
// Enter: .user-content ul:not(.class1):not(.class1)
// ==========================================================================
$not-list-classes: ':not(.reset-list)';


// Main Navigation
// ==========================================================================
$main-nav-height--spec: 80px;
$main-nav-height--spec--sm: 60px;


// Button State
// ==========================================================================
/// @description: Примесь добавляет на кнопки настроек специальной версии выд для сотояний: :hover, :focus, .active
@mixin spec-button-states() {
	// marker arrow
	&::before, &::after {
		display: none;
		content: "";
		position: absolute;
		//left: 50%
		left: 3px;
		//margin: -1px 0 -1px -#{round($spec-panel__btn-arr-size / 2)}
		margin: 3px 0;
		pointer-events: none;

		border-top: 3px solid currentColor;
		width: calc(100% - 6px); }

	&::before {
		bottom: 100%;
 }		//+triangle("bottom", currentColor, round($spec-panel__btn-arr-size / 2), 0.8)

	&::after {
		top: 100%;
 }		//+triangle("top", currentColor, round($spec-panel__btn-arr-size / 2), 0.8)

	// hover, focus
	&:hover, &:focus {
		outline: none;
		box-shadow: $spec-panel__btn-shadow-inside $spec-bg-color, $spec-panel__btn-shadow-outside $spec-text-color;

		.#{$spec-mod--scheme-wb} & {
			box-shadow: $spec-panel__btn-shadow-inside $spec-bg-color--scheme-wb, $spec-panel__btn-shadow-outside $spec-text-color--scheme-wb; }

		.#{$spec-mod--scheme-blue} & {
			box-shadow: $spec-panel__btn-shadow-inside $spec-bg-color--scheme-blue, $spec-panel__btn-shadow-outside $spec-text-color--scheme-blue; }

		.#{$spec-mod--scheme-brown} & {
			box-shadow: $spec-panel__btn-shadow-inside $spec-bg-color--scheme-brown, $spec-panel__btn-shadow-outside $spec-text-color--scheme-brown; }

		.#{$spec-mod--scheme-green} & {
			box-shadow: $spec-panel__btn-shadow-inside $spec-bg-color--scheme-green, $spec-panel__btn-shadow-outside $spec-text-color--scheme-green; } }

	// active
	&.active {
		&:hover, &:focus {
			box-shadow: $spec-panel__btn-shadow-hover currentColor, $spec-panel__btn-shadow-inside $spec-bg-color, $spec-panel__btn-shadow-outside $spec-text-color;

			.#{$spec-mod--scheme-wb} & {
				box-shadow: $spec-panel__btn-shadow-hover currentColor, $spec-panel__btn-shadow-inside $spec-bg-color--scheme-wb, $spec-panel__btn-shadow-outside $spec-text-color--scheme-wb; }

			.#{$spec-mod--scheme-blue} & {
				box-shadow: $spec-panel__btn-shadow-hover currentColor, $spec-panel__btn-shadow-inside $spec-bg-color--scheme-blue, $spec-panel__btn-shadow-outside $spec-text-color--scheme-blue; }

			.#{$spec-mod--scheme-brown} & {
				box-shadow: $spec-panel__btn-shadow-hover currentColor, $spec-panel__btn-shadow-inside $spec-bg-color--scheme-brown, $spec-panel__btn-shadow-outside $spec-text-color--scheme-brown; }

			.#{$spec-mod--scheme-green} & {
				box-shadow: $spec-panel__btn-shadow-hover currentColor, $spec-panel__btn-shadow-inside $spec-bg-color--scheme-green, $spec-panel__btn-shadow-outside $spec-text-color--scheme-green; } } }

	&.active {
		&[tabindex="-1"] {
			cursor: default; }

		// Показывать активное состояние для всех кнопок,
		// но наведение и фокус не показыать только для кнопок с tabindex="-1"
		&, &[tabindex="-1"]:hover, &[tabindex="-1"]:focus {
			box-shadow: $spec-panel__btn-shadow-hover currentColor; }

		&::before, &::after {
			display: block;

			@include media('<mob-md') {
				display: none; } } } }

// Nav
$header-height: 80px !default;
$header-height--md: 60px !default;
$header-height--sm: 50px !default;

$nav-width: 100% !default;
$nav-height: 49px;
$nav-space: 8px 24px !default;
$nav-space--sm: 8px 15px !default;
$nav-space--mob: 12px 15px !default;
$nav-arrow-size: 32px !default;
$nav-text-color: $spec-text-color !default;

$z-i-nav: 9997 !default;
$z-i-header: 9998 !default;

// Animation
// ==========================================================================
$animation-speed: 0.1s !default;
$animation-speed-mouseleave: 0.35s !default;
